.hero-banner {
	position: relative;
	height: 60vh;
	overflow: hidden;
	align-items: center;
	
	> img {
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translateY(-50%)translateX(-50%);
		width: 100%;
		height: auto;
		z-index: 1;	
	}
	
	.youtube-background {
		z-index: 1;
	}
	
	.cell {
		position: relative;
		z-index: 2;
		text-align: center;
		color: white;
		
		* {
			color: white;
		}
		
	}
	
		
}

.hero-slider {
	
	.hero-slide {
		align-items: flex-end;
		
	}
	
}